export default {
	data() {
		return {
			count: "",
			phone: uni.getStorageSync('phone'),
			k_cash: 0,
			realcount: 0,
			tpye:0
		}
	},
	onLoad(e) {
		this.k_cash = e.k_cash
		this.type = e.type
	},
	methods: {
		cash(count) {
			this.realcount = (count * 0.95).toFixed(2)
		},
		togetMoney() {
			
			var count = Number(this.count)
			console.log(this.k_cash)
			if (count > 0) {
				if (count <= this.k_cash) {
					if(this.type==0){
						
					uni.request({
						url: this.$bashUrl + '/index.php/home/Api/t_cash',
						method: "POST",
						data: {
							phone: this.phone,
							cash: count
						},
						success: res => {
							console.log(res)
							uni.showToast({
								title: "提现处理中"
							})
							setTimeout(() => {
								uni.reLaunch({
									url: "../me/me"
								})
							}, 2000)
						}
					})
					}else if(this.type==1){
						uni.request({
							url: this.$bashUrl + '/index.php/home/Api/t_t_cash',
							method: "POST",
							data: {
								phone: this.phone,
								cash: count
							},
							success: res => {
								console.log(res)
								uni.showToast({
									title: "提现处理中"
								})
								setTimeout(() => {
									uni.reLaunch({
										url: "../me/me"
									})
								}, 2000)
							}
						})
					}
				} else {
					uni.showToast({
						title: '可提现金额不足',
						icon: 'none'
					})
				}
			} else {
				uni.showToast({
					title: '提现金额不能为0',
					icon: 'none'
				})
			}

		}
	}
}
